*, :before, :after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

ul {
  list-style: none;
}

a, img, data, span, input, button, select, ion-icob, textarea {
  display: block;
}

a {
  color: inherit;
  text-decoration: none;
}

img {
  height: auto;
}

input, button, select, textarea {
  font: inherit;
  background: none;
  border: none;
}

input, select, textarea {
  width: 100%;
  outline: none;
}

button {
  cursor: pointer;
}

address {
  font-style: normal;
}

html {
  scroll-behavior: smooth;
  font-size: 62.5%;
}

body {
  background-color: var(--eerie-black-1);
  color: var(--white);
  font-family: var(--fontFamily-main);
  font-size: var(--fontSize-body-4);
  font-weight: var(--weight-regular);
  line-height: var(--lineHeight-5);
  overflow: hidden;
}

body.loaded {
  overflow: auto;
  overflow: overlay;
}

body.active {
  overflow: hidden;
}

::-webkit-scrollbar {
  width: 5px;
}

::-webkit-scrollbar-track {
  background-color: rgba(0, 0, 0, 0);
}

::-webkit-scrollbar-thumb {
  background-color: var(--c-dark-red);
}

.display-1, .headline-1, .headline-2, .title-1, .title-2, .title-3, .title-4 {
  color: var(--white);
  font-family: var(--fontFamily-title);
  font-weight: var(--weight-regular);
  line-height: var(--lineHeight-2);
}

.display-1 {
  font-size: var(--fontSize-display-1);
  line-height: var(--lineHeight-1);
}

.headline-1 {
  font-size: var(--fontSize-headline-1);
}

.headline-2 {
  font-size: var(--fontSize-headline-2);
  line-height: var(--lineHeight-6);
}

.title-1 {
  font-size: var(--fontSize-title-1);
}

.title-2 {
  font-size: var(--fontSize-title-2);
}

.title-3 {
  font-size: var(--fontSize-title-3);
}

.title-4 {
  font-size: var(--fontSize-title-4);
}

.body-1 {
  font-size: var(--fontSize-body-1);
  line-height: var(--lineHeight-6);
}

.body-2 {
  font-size: var(--fontSize-body-2);
  line-height: var(--lineHeight-4);
}

.body-3 {
  font-size: var(--fontSize-body-3);
}

.body-4 {
  font-size: var(--fontSize-body-4);
}

.label-1 {
  font-size: var(--fontSize-label-1);
}

.label-2 {
  font-size: var(--fontSize-label-2);
}

.text-center {
  text-align: center;
}

:root {
  --c-bright-red: #f60000;
  --c-dark-red: #c90015;
  --c-light-grey: #f3f3f3;
  --c-grey-alpha-50: rgba(243, 243, 243, .5);
  --c-grey: #d3d3d3;
  --c-medium-grey: #333;
  --c-dark-grey: #080809;
  --max-width: 1170px;
  --quick-silver: #a6a6a6;
  --davys-grey: #595754;
  --smoky-black-1: #0e0d0b;
  --smoky-black-2: #0e0d0c;
  --smoky-black-3: #121111;
  --eerie-black-1: #161718;
  --eerie-black-2: #1b1c1d;
  --eerie-black-3: #141515;
  --eerie-black-4: #212121;
  --white: #fff;
  --white-alpha-50: rgba(255, 255, 255, .5);
  --white-alpha-20: rgba(255, 255, 255, .2);
  --white-alpha-10: rgba(255, 255, 255, .1);
  --black: #000;
  --black-alpha-80: rgba(0, 0, 0, .8);
  --black-alpha-15: rgba(0, 0, 0, .15);
  --loading-text-gradient: linear-gradient( 90deg, transparent 0% 16.66%, var(--c-dark-red) 33.33% 50%, transparent 66.66% 75% );
  --gradient-1: linear-gradient( to top, rgba(0, 0, 0, .9), rgba(0, 0, 0, .7), transparent );
  --fontFamily-title: "Dancing Script", cursive;
  --fontFamily-main: "Roboto Condensed", serif;
  --fontSize-display-1: calc(1.3rem + 6.7vw);
  --fontSize-headline-1: calc(2rem + 2.5vw);
  --fontSize-headline-2: calc(1.3rem + 2.4vw);
  --fontSize-title-1: calc(1.6rem + 1.2vw);
  --fontSize-title-2: 2.2rem;
  --fontSize-title-3: 2.1rem;
  --fontSize-title-4: calc(1.6rem + 1.2vw);
  --fontSize-body-1: 2.4rem;
  --fontSize-body-2: 1.6rem;
  --fontSize-body-3: 1.8rem;
  --fontSize-body-4: 1.6rem;
  --fontSize-label-1: 1.4rem;
  --fontSize-label-2: 1.2rem;
  --weight-regular: 400;
  --weight-bold: 700;
  --weight-extrabold: 900;
  --lineHeight-1: 1em;
  --lineHeight-2: 1.2em;
  --lineHeight-3: 1.5em;
  --lineHeight-4: 1.6em;
  --lineHeight-5: 1.85em;
  --lineHeight-6: 1.4em;
  --letterSpacing-1: .15em;
  --letterSpacing-2: .4em;
  --letterSpacing-3: .2em;
  --letterSpacing-4: .3em;
  --letterSpacing-5: 3px;
  --section-space: 70px;
  --shadow-1: 0px 0px 25px 0px rgba(0, 0, 0, .25);
  --radius-24: 24px;
  --radius-circle: 50%;
  --transition-1: 250ms ease;
  --transition-2: 500ms ease;
  --transition-3: 1000ms ease;
}

@media (min-width: 575px) {
  :root {
    --fontSize-body-2: 2rem;
  }
}

@media (min-width: 992px) {
  :root {
    --section-space: 10rem;
  }
}

@media (min-width: 1200px) {
  :root {
    --fontSize-title-2: 2.5rem;
  }
}

@keyframes loading {
  0% {
    background-position: 100%;
  }

  100% {
    background-position: 0%;
  }
}

@keyframes menuBtn {
  0% {
    transform: scaleX(1);
  }

  100% {
    transform: scaleX(.5);
  }
}

@keyframes sliderReveal {
  0% {
    opacity: 0;
    transform: translateY(30px);
  }

  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes rotate360 {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(1turn);
  }
}

@keyframes move {
  0%, 100% {
    transform: translateY(0);
  }

  50% {
    transform: translateY(30px);
  }
}

@keyframes smoothScale {
  0% {
    transform: scale(1);
  }

  100% {
    transform: scale(1.15);
  }
}

.hover-underline {
  max-width: max-content;
  position: relative;
}

.hover-underline:after {
  content: "";
  width: 100%;
  height: 5px;
  border-block: 1px solid var(--c-dark-red);
  opacity: 0;
  transition: var(--transition-2);
  position: absolute;
  bottom: 0;
  left: 0;
  transform: scaleX(.2);
}

.hover-underline:hover:after, .hover-underline:focus:after {
  opacity: 1;
  transform: scaleX(1);
}

.hover-shine {
  position: relative;
}

.hover-shine:after {
  content: "";
  width: 50%;
  height: 100%;
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0) 0%, rgba(255, 255, 255, .4) 100%);
  position: absolute;
  top: 0;
  left: 0;
  transform: skewX(-.08turn)translateX(-180%);
}

.hover-shine:hover:after, .hover-shine:focus-within:after {
  transition: var(--transition-3);
  transform: skewX(-.08turn)translateX(275%);
}

.move-anim {
  animation: move 5s linear infinite;
}

.bg-black-10 {
  background-color: var(--smoky-black-1);
}

.hero__book {
  z-index: 1;
  position: relative;
}

.hero__book:after {
  content: "";
  position: absolute;
}

.loader {
  background-color: var(--c-light-grey);
  z-index: 10;
  transition: var(--transition-2);
  align-content: center;
  justify-content: center;
  justify-items: center;
  display: grid;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.loader > * {
  transition: var(--transition-1);
}

.loader.loaded {
  transition-delay: 250;
  transform: translateY(100%);
}

.loader.loaded > * {
  opacity: 0;
  animation-name: none;
}

.loader__circle {
  width: 10rem;
  height: 10rem;
  border-radius: var(--radius-circle);
  border: 3px solid var(--c-dark-red);
  border-top-color: var(--c-light-grey);
  margin-bottom: 1.5rem;
  animation: rotate360 .8s linear infinite;
}

.loader__text {
  background-image: var(--loading-text-gradient);
  font-size: 4rem;
  font-weight: var(--weight-bold);
  text-transform: uppercase;
  letter-spacing: 1.6rem;
  -webkit-text-fill-color: transparent;
  background-size: 500%;
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-stroke: .5px var(--c-dark-red);
  animation: loading 2s linear infinite;
}

