.container {
  padding: 1.6rem;
}

@media (min-width: 768px) {
  :is(.service, .events) .container {
    max-width: 82rem;
  }
}

@media (min-width: 992px) {
  :is(.service, .events) .container {
    max-width: unset;
  }
}

@media (min-width: 1200px) {
  :is(.service, .events) .container {
    max-width: 120rem;
