.btn {
  font-size: var(--fontSize-label-1);
  font-weight: var(--weight-bold);
  transition: var(--transition-1);
  text-transform: uppercase;
  letter-spacing: var(--letterSpacing-5);
  max-width: max-content;
  border: 2px solid var(--c-dark-red);
  z-index: 1;
  color: var(--c-dark-red);
  background-color: var(--c-light-grey);
  padding: .8rem 2.5rem;
  position: relative;
  overflow: hidden;
}

.btn:before {
  content: "";
  width: 200%;
  height: 200%;
  border-radius: var(--radius-circle);
  background-color: var(--c-dark-red);
  transition: var(--transition-2);
  z-index: -1;
  position: absolute;
  bottom: 100%;
  left: 50%;
  transform: translateX(-50%);
}

.btn--dark {
  color: var(--c-bright-red);
  background-color: var(--c-dark-grey);
}

.btn--transparent {
  color: var(--c-bright-red);
  background-color: rgba(0, 0, 0, 0);
}

.btn:hover:before, .btn:focus-visible:before {
  bottom: -50%;
}

.btn__behind {
  min-width: max-content;
  color: var(--c-light-grey);
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translateX(-50%);
}

.btn:hover .btn__forward, .btn:focus-visible .btn__forward {
  transform: translateY(-40px);
}

.btn:hover .btn__behind, .btn:focus-visible .btn__behind {
  top: 50%;
  transform: translate(-50%, -50%);
}

.btn__text {
  color: var(--c-bright-red);
  letter-spacing: var(--letterSpacing-3);
  transition: var(--transition-1);
  margin-left: auto;
  margin-right: auto;
  padding-bottom: .4rem;
}

.btn__text:hover, .btn__text:focus-visible {
  color: var(--white);
}

.separator {
